<template>
    <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeIn"
    >
        <van-list
                v-model="loading"
                :finished="noMore"
                finished-text="没有更多了"
                @load="onLoad"
        >
            <ul class="conList">
                <li class="ov" v-for="item in transferList">
                    <img class="fl" src="@/static/images/my/zhuanzhan_icon@2x.png" alt="">
                    <a class="fl ov" href="javascript:void (0)">
                        <div class="fl"><p class="text1">{{item.tousername}}</p><p class="text2">{{item.create_time}}</p></div>
                        <span class="fr zhuangzhang">{{item.money}} USDT</span>
                    </a>
                </li>
            </ul>
        </van-list>
    </transition>
</template>

<script>
    import {transferList} from "@/api/center";
    import { List } from 'vant';
    export default {
        name: "tansferlist",
        components:{
            [List.name]: List,
        },
        data()
        {
            return {
                transferList:[],
                noMore:false,
                loading:false,
                page:1
            }
        },
        mounted()
        {
           this.onLoad()
        },
        methods:{
            onLoad () {
                this.loading = true
                this.gettransferList();
            },

            gettransferList()
            {
                transferList({page:this.page}).then(res => {
                    this.loading = false
                    if (res.length < 10)
                    {
                        this.noMore = true
                    }
                    if (this.page == 1)
                    {
                        this.transferList = res
                    } else
                    {
                        this.transferList = this.transferList.concat(res)
                    }
                    this.page++

                })
            }
        }
    }
</script>

<style scoped>
    .conList {
        padding: 0 0.15rem;
        background: #fff;
    }
    .conList li {
        width: 3.45rem;
        height: 0.7rem;
        border-bottom: 1px solid #F6F6F6;
    }
    .conList li img {
        width: 0.3rem;
        height: 0.3rem;
        margin-top: 0.2rem;
    }
    .conList li a {
        width: 3.15rem;
    }
    .conList li a div {
        margin-left: 0.15rem;
        margin-top: 0.18rem;
    }
    .conList li a div .text1 {
        color: #333333;
        font-size: 0.14rem;
        width: 1.5rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .conList li a div .text2 {
        color: #AAAAAA;
        font-size: 0.12rem;
        width: 1.5rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .conList li a span {
        line-height: 0.7rem;
        font-size: 0.16rem;
    }
    .conList li a .tixian {
        color: #FC7C15;
    }
    .conList li a .zhuangzhang {
        color: #333333;
    }
</style>



