<template>
    <div class="tabBar">
        <ul>
            <router-link to="/" tag="li" class="tabHome" :class="{active:key == '/'}"><a href="javascript:void(0)"><i></i><p>首页</p></a></router-link>
            <router-link to="/waterd" tag="li" class="tabHb" :class="{active:key == '/waterd'}"><a href="javascript:void(0)"><i></i><p>水滴还</p></a></router-link>
            <router-link to="/rule" tag="li" class="tabCash" :class="{active:key == '/rule'}"><a href="javascript:void(0)"><i></i><p>规则</p></a></router-link>
            <router-link to="/mine" tag="li" class="tabMy" :class="{active:key == '/mine'}"><a href="javascript:void(0)"><i></i><p>我的</p></a></router-link>
        </ul>
    </div>
</template>

<script>
    import {must} from "../api";
    import { toast, Dialog } from 'vant';
    export default {
        name: "tabBar",
        computed: {
            key()
            {
                return this.$route.path
            }
        },
        mounted()
        {
            this.getMustInfo()
        },
        methods:{
            getMustInfo()
            {
                must().then(res => {

                    // Android判断
                    if (this.$phonemodel == 'Android')
                    {
                        if (res.version != this.$version)
                        {
                            Dialog.alert({
                            title: '系统提醒',
                            message: '当前版本不是最新版, 请立即升级到最新版',
                                beforeClose:this.closeDialog
                        }).then(() => {


                        });

                        }
                    }else if (res.notice){
                        Dialog.alert({
                            title: '系统提醒',
                            message: '恭喜您赚取了'+res.notice.money_rmb+'元（'+res.notice.money+'usdt）的收益，资金已入账，请到个人资金内查看'
                        }).then(() => {
                            // on close
                        });
                    }

                })
            },
            closeDialog()
            {
                window.location.href = 'http://shuidihuan.name/download/waterd_Android_apk.apk';
                //done(false)
            }
        }
    }
</script>

<style scoped>
    /* 底部tabBar */
    .tabBar {
        width: 100%;
        height: 0.48rem;
        background: #fff;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.24);
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 999;
    }
    .tabBar ul {
        overflow: hidden;
    }
    .tabBar ul li {
        width: 25%;
        float: left;
        text-align: center;
    }
    .tabBar ul li .showHide {
        width: 100%;
        background: #fff;
        position: fixed;
        bottom: 0.48rem;
        left: 0;
        z-index: 1000;
        display: none;
        box-shadow: 0 -6px 10px 0 rgba(0, 0, 0, 0.1);
        border-radius: 0.12rem 0.12rem 0 0;
    }
    .tabBar ul li .showHide a {
        display: block;
        padding: 0.14rem 0;
        border-bottom: 0.01rem solid #f5f5f5;
        font-size: 0.15rem;
        color: #131313;
    }
    .tabBar ul li .showHide a span {
        vertical-align: middle;
    }
    .tabBar ul li .showHide a img {
        width: 0.3rem;
        height: 0.3rem;
        margin-right: 0.14rem;
        vertical-align: middle;
    }
    .tabBar ul li a {
        display: block;
        padding: 0.05rem 0;
    }
    .tabBar ul .tabHome a i {
        width: 0.2rem;
        height: 0.2rem;
        display: block;
        margin: auto;
        background: url('../static/images/tab/shouye_icon_normal@2x.png') no-repeat center;
        background-size: 100% 100%;
    }
    .tabBar ul .tabHome a p {
        font-size: 0.12rem;
        color: #979797;
        margin-top: 0.02rem;
    }
    .tabBar ul .tabHome.active a i {
        width: 0.2rem;
        height: 0.2rem;
        display: block;
        margin: auto;
        background: url('../static/images/tab/shouye_icon_cilck@2x.png') no-repeat center;
        background-size: 100% 100%;
    }
    .tabBar ul .tabHome.active a p {
        color: #4B89E7;
        font-size: 0.12rem;
        margin-top: 0.02rem;
    }
    .tabBar ul .tabHb a i {
        width: 0.2rem;
        height: 0.2rem;
        display: block;
        margin: auto;
        background: url('../static/images/tab/shuidi_icon_normal@2x.png') no-repeat center;
        background-size: 100% 100%;
    }
    .tabBar ul .tabHb a p {
        font-size: 0.12rem;
        color: #979797;
        margin-top: 0.02rem;
    }
    .tabBar ul .tabHb.active a i {
        width: 0.2rem;
        height: 0.2rem;
        display: block;
        margin: auto;
        background: url('../static/images/tab/shuidi_icon_cilck@2x.png') no-repeat center;
        background-size: 100% 100%;
    }
    .tabBar ul .tabHb.active a p {
        color: #4B89E7;
    }
    .tabBar ul .tabCash a i {
        width: 0.2rem;
        height: 0.2rem;
        display: block;
        margin: auto;
        background: url('../static/images/tab/guize_icon_normal@2x.png') no-repeat center;
        background-size: 100% 100%;
    }
    .tabBar ul .tabCash a p {
        font-size: 0.12rem;
        color: #979797;
        margin-top: 0.02rem;
    }
    .tabBar ul .tabCash.active a i {
        width: 0.2rem;
        height: 0.2rem;
        display: block;
        margin: auto;
        background: url('../static/images/tab/guize_icon_click@2x.png') no-repeat center;
        background-size: 100% 100%;
    }
    .tabBar ul .tabCash.active a p {
        color: #4B89E7;
    }
    .tabBar ul .tabMy a i {
        width: 0.2rem;
        height: 0.2rem;
        display: block;
        margin: auto;
        background: url('../static/images/tab/person_icon_normal@2x.png') no-repeat center;
        background-size: 100% 100%;
    }
    .tabBar ul .tabMy a p {
        font-size: 0.12rem;
        color: #979797;
        margin-top: 0.02rem;
    }
    .tabBar ul .tabMy.active a i {
        width: 0.2rem;
        height: 0.2rem;
        display: block;
        margin: auto;
        background: url('../static/images/tab/person_icon_click@2x.png') no-repeat center;
        background-size: 100% 100%;
    }
    .tabBar ul .tabMy.active a p {
        color: #4B89E7;
    }
</style>



