<template>
    <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeIn"
    >
        <van-list
                v-model="loading"
                :finished="noMore"
                finished-text="没有更多了"
                @load="onLoad"
        >
            <ul class="conList">

                <li class="ov" v-for="item in withdrawList">
                    <img class="fl" src="@/static/images/my/tixian_icon@2x.png" alt="">
                    <a class="fl ov" href="javascript:void (0)">
                        <div class="fl"><p class="text1">{{item.address}}</p><p class="text2">{{item.create_time}}</p></div>
                        <div class="fr">
                            <p v-if="item.state == 1" class="wait text3">待审核</p><p v-if="item.state == 2" class="warning text3">待入账</p><p v-if="item.state == 3" class="danger text3">已拒绝</p><p v-if="item.state == 4" class="succ text3">已完成</p>
                        <p class="text4">{{item.total}} USDT</p></div>
                    </a>
                </li>

            </ul>
        </van-list>
    </transition>
</template>

<script>
    import {withdrawList} from "@/api/center";
    import { List } from 'vant';
    export default {
        name: "cashlist",
        components:{
            [List.name]: List,
        },
        data()
        {
            return {
                withdrawList:[],
                noMore:false,
                loading:false,
                page:1
            }
        },
        methods:{
            onLoad () {
                this.loading = true
                this.getWithdrawList();
            },
            getWithdrawList()
            {
                withdrawList({page:this.page}).then(res => {
                    this.loading = false
                    if (res.length < 10)
                    {
                        this.noMore = true
                    }
                    if (this.page == 1)
                    {
                        this.withdrawList = res
                    } else
                    {
                        this.withdrawList = this.withdrawList.concat(res)
                    }
                    this.page++

                })
            }
        }
    }
</script>

<style scoped>
    .conList {
        padding: 0 0.15rem;
        background: #fff;
    }
    .conList li {
        width: 3.45rem;
        height: 0.7rem;
        border-bottom: 1px solid #F6F6F6;
    }
    .conList li img {
        width: 0.3rem;
        height: 0.3rem;
        margin-top: 0.2rem;
    }
    .conList li a {
        width: 3.15rem;
    }
    .conList li a div {
        margin-left: 0.15rem;
        margin-top: 0.18rem;
    }
    .conList li a div .text1 {
        color: #333333;
        font-size: 0.14rem;
        width: 1.5rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .conList li a div .text2 {
        color: #AAAAAA;
        font-size: 0.12rem;
        width: 1.5rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .conList li a div .text3 {
        /*color: #13a09b;*/
        font-size: 0.14rem;
        width: 1rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        text-align: right;
    }

    .conList li a div .text4 {
        color: #AAAAAA;
        font-size: 0.12rem;
        width: 1rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        text-align: right;
    }

    .conList li a div .succ{color: #1ba734;}
    .conList li a div  .danger{color: #F56C6C;}
    .conList li a div  .warning{color: #E6A23C;}
    .conList li a div  .wait{color: #409EFF;}
    .conList li a span {
        line-height: 0.7rem;
        font-size: 0.16rem;
    }
    .conList li a .tixian {
        color: #FC7C15;
    }
    .conList li a .zhuangzhang {
        color: #333333;
    }
</style>



