<template>
    <div class="navBox"><i @click="goback"></i><p>{{title}}</p ></div>
</template>

<script>
    export default {
        name: "navbar",
        props:['title', 'path'],
        methods:{
            goback()
            {
                if (this.path)
                {
                    this.$router.replace(this.path)

                } else
                {
                    this.$router.go(-1)
                }

            }
        }
    }
</script>

<style scoped>
    .navBox {
        height: .44rem;
        line-height: .44rem;
        background: #fff;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 999;
        border-bottom: 1px solid #f5f5f5;
    }
    .navBox p{
        text-align: center;
        color: #304A61;
        font-size: .16rem;
        font-weight:bold;
    }
    .navBox i{
        position: absolute;
        background: url("../static/images/index/ret.png") no-repeat center;
        background-size: .17rem .17rem;
        width: .4rem;
        height: .4rem;
        display: inline-block;
        left: 0;
        top: .02rem;
    }

</style>



